<template>
  <div>
    <h2></h2>
    <p> popover  text</p>
    <h3>popover </h3>
    <demo>
      <vue-form :model="form" :layer="layer">
        <vue-form-line :cols="[{ span: 12, label: '', prop: '/form/name' }]">
          <el-input v-model="form.name" placeholder="" />
        </vue-form-line>
        <vue-form-line :cols="[{ span: 12, label: '', prop: '/form/type' }]">
          <el-input v-model="form.name" placeholder="" />
        </vue-form-line>
      </vue-form>
      <div slot="description">
        <p> prop </p>
      </div>
      <div slot="meta">
        <pre><code class="hljs hl-html"><span class="hljs-tag">&lt;vue-form :model=<span class="hljs-string">"form"</span> :layer=<span class="hljs-string">"layer"</span>&gt;
    &lt;vue-form-line :cols=<span class="hljs-string">"[{ span: 12, label: '', prop: '/form/name' }]"</span>&gt;
      &lt;el-input v-model=<span class="hljs-string">"form.popover"</span> placeholder=<span class="hljs-string">""</span> /&gt;
    &lt;/vue-form-line&gt;
  &lt;/vue-form&gt;
  &lt;script&gt;
  export default <span class="hljs-attr">{
    data() {
      <span class="hljs-tag">return</span> {
        form: {
          name: "",
          type: ""
        },
        layer: [
          {
            data: [
              {
                prop: "/form/name",
                data: ""
              },
              {
                prop: "/form/type",
                data: "",
                type: "text"
              }
            ]
          }
        ]
      }
    }
  }</span>
  &lt;/script&gt;</span></code></pre>
      </div>
    </demo>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        name: "",
        type: ""
      },
      layer: [
        {
          data: [
            {
              prop: "/form/name",
              data: ""
            },
            {
              prop: "/form/type",
              data: "",
              type: "text"
            }
          ]
        }
      ]
    };
  }
};
</script>

<style>
</style>



