<template>
  <div>
    <h2>Basic </h2>
    <p> vue-form-line element</p>
    <demo>
      <vue-form :model="form" :rowledge="24">
        <vue-form-line label="">
          <el-input v-model="form.name" placeholder="" />
          <el-input v-model="form.type" placeholder="" />
        </vue-form-line>
        <vue-form-line label="" :cols="[{ span: 8 },{ span: 8 }, { span: 8 }]" :span="12">
          <el-input v-model="form.long" placeholder="" />
          <el-input v-model="form.width" placeholder="" />
          <el-input v-model="form.height" placeholder="" />
        </vue-form-line>
        <vue-form-line :cols="[{ label: '' }]" :span="12">
          <el-input v-model="form.weight" placeholder="">
            <template slot="append">kg</template>
          </el-input>
        </vue-form-line>
      </vue-form>
      <div slot="description">
        <p></p>
      </div>
      <div slot="meta">
        <vue-markdown v-highlight :source="content"></vue-markdown>
      </div>
    </demo>
  </div>
</template>

<script>
export default {
  data() {
    return {
      content: `
    <template>
      <vue-form :model="form" :rowledge="24">
        <vue-form-line :cols="[{ label: '' },{ label: '' }]">
          <el-input v-model="form.name" placeholder="" />
          <el-input v-model="form.type" placeholder="" />
        </vue-form-line>
        <vue-form-line label="" :span="12">
          <el-input v-model="form.long" placeholder="" />
          <el-input v-model="form.width" placeholder="" />
          <el-input v-model="form.height" placeholder="" />
        </vue-form-line>
        <vue-form-line :cols="[{ label: '', span: '14' }]" :span="12">
          <el-input v-model="form.weight" placeholder="" />
          <el-button>search</el-button>
        </vue-form-line>
      </vue-form>
    <\/template>

    <script>
      export default {
        data() {
          return {
            form: {
              name: "",
              type: "",
              long: "",
              width: "",
              height: ""
            }
          }
        }
      }
    <\/script>
      `,
      form: {
        name: "",
        type: "",
        long: "",
        width: "",
        height: ""
      }
    };
  }
};
</script>

<style>
</style>



